import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../components/HomeView.vue'
import SignUp from '../components/SignUp.vue'
import LogIn from '../components/LogIn.vue'
import AddCourses from '../components/AddCourses.vue'
import UpdateCourses from '../components/UpdateCourses.vue'
import ViewCourses from '../components/ViewCourses.vue'
import DeleteCourses from '../components/DeleteCourses.vue'

const routes = [
  {
    path: '/sign-up',
    name: 'SignUp',
    component: SignUp
  },
  {
    path: '/login',
    name: 'LogIn',
    component: LogIn
  },
  {
    path: '/',
    name: 'HomeView',
    component: HomeView,
  },
  {
    path: '/add-courses',
    name: 'AddCourses',
    component: AddCourses,
  },
  {
    path: '/update-course/:id',
    name: 'UpdateCourses',
    component: UpdateCourses,
  },
  {
    path: '/delete-course/:id',
    name: 'DeleteCourses',
    component: DeleteCourses,
  },
  {
    path: '/view-courses',
    name: 'ViewCourses',
    component: ViewCourses,
  }
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
