
<template>
    <HeaderView/>
    
    <div class="container" id="ViewCoursesID">
        <h1 class="text-left text-white fs-1 fw-bold">ðŸ˜Š {{name}} See Your Courses</h1>
        <div class="row mt-3">
            <div class="col-md-4 mt-5 card" style="width:300px" v-for="item in course" :key="item.id" >
              <iframe class="embed-responsive-item" v-bind:src="`https://www.youtube.com/embed/${item.videoId}`" allowfullscreen></iframe>
              <div class="card-body">
                <h5 class="card-title"> {{item.name}}</h5>
                <p class="card-text"> {{item.description}}</p>
                <button class="btn "><router-link id="routerLinkID1" :to="`/update-course/${item.id}`">Edit</router-link></button>
                <button class="btn"><router-link id="routerLinkID2" :to="`/delete-course/${item.id}`">Delete</router-link></button>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
import HeaderView from './HeaderView.vue'
import axios from 'axios'
export default{
    name:'ViewCourses',
    data(){
        return{
            name:'',
            course:[]
        }
    },
    beforeCreate () {
        document.querySelector('body').setAttribute('style', 'background:black')
    },
    components:{
        HeaderView
    },
    async mounted(){
        let user = localStorage.getItem('user-info');
        this.name = JSON.parse(user).name
        if(!user){
            this.$router.push({name:'SignUp'})
        }
        let result = await axios.get("http://localhost:5000/tutor/view-courses");
        console.warn(result)
        this.course = result.data;
    }
}
</script>


<style scoped>
h1{
    font-weight: 500;
    font-size: 3rem;
}
.card-title{
  font-size: 1.5rem;
  color:white;
}
.card{
  background: black;
    margin:1%;
    border-radius: 20px;
}
#ViewCoursesID{
    margin-left: 250px;
}
.btn{
  background: #d51987;
  color: black;
  margin-right: 3%;
}
#routerLinkID1{
  color:black;
  font-size: 1rem;
  text-decoration: none;
}
#routerLinkID2{
  color:black;
  font-size: 1rem;
  text-decoration: none;
}
</style>